@charset "UTF-8";
html {
  height: 100%;
  touch-action: manipulation; }

body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  word-break: break-word;
  background-color: #eee;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

img {
  vertical-align: bottom; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

p {
  margin: 0; }

#root {
  width: 425px;
  height: 100%;
  margin: 0 auto;
  padding: 0;
  background-color: #fff; }
  @media (max-width: 425px) {
    #root {
      width: 100%; } }

*:focus {
  outline: none; }
