.Maintenance {
  display: flex;
  flex-direction: column;
  height: 100%; }
  .Maintenance .message-area {
    flex-grow: 1;
    padding: 20px;
    background-image: url("../../assets/img/maintenance_bg.png");
    background-repeat: no-repeat;
    background-size: cover; }
    .Maintenance .message-area .logo {
      display: block;
      width: 80%;
      margin: auto; }
    .Maintenance .message-area .text-area {
      margin: 20px 0;
      padding: 20px;
      background-color: rgba(255, 255, 255, 0.75); }
      .Maintenance .message-area .text-area .title {
        padding-bottom: 1rem;
        font-weight: 600;
        font-size: 20px;
        text-align: center; }
      .Maintenance .message-area .text-area .sub-title {
        font-weight: 600;
        font-size: 16px; }
      .Maintenance .message-area .text-area .time {
        padding: 1rem;
        color: red;
        font-weight: 600;
        font-size: 16px; }
      .Maintenance .message-area .text-area .message {
        font-size: 15px; }
        .Maintenance .message-area .text-area .message .paragraph {
          margin-top: 1em; }
